import React from "react"
import { Link } from "react-router-dom"

export const LinksList = ({ links }) => {
    if (!links.length) return <p className="center">No links for now</p>

    return (
        <table>
            <thead>
                <tr>
                    <th>â„–</th>
                    <th>Original</th>
                    <th>Shortened</th>
                    <th>Open</th>
                </tr>
            </thead>

            <tbody>
                {links.map((link, idx) => (
                    <tr key={link._id}>
                        <td>{idx + 1}</td>
                        <td>{link.from}</td>
                        <td>{link.to}</td>
                        <td>
                            <Link to={`/details/${link._id}`}>Open</Link>
                        </td>
                    </tr>
                ))}
            </tbody>
        </table>
    )
}
